.app {
  .app-bar {
    background-color: white;
    border-bottom: solid 1px #c5c5c5;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .spawner-cntr {
    min-height: 100vh;
    background-color: #F7F9F9;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    padding: 0.8rem 0.5rem;
    flex-wrap: wrap;
    .spawner {
      width: 18rem;
      margin-bottom: 1rem;
      .ant-card-body {
        padding: 12px 24px;
      }
      .select {
        width: 100%;
      }
      .form {
        margin-bottom: 0.5rem;
        .title {
          font-weight: 600;
        }
      }
    }
  }
  .mb {
    margin-bottom: 0.5rem;
  }
}
